.wp-block-spacer {
	&.spacer-1 {
		@apply h-4 !important;
	}
	&.spacer-2 {
		@apply h-8 !important;
	}
	&.spacer-3 {
		@apply h-12 !important;
	}
	&.spacer-4 {
		@apply h-16 !important;
	}
	&.spacer-5 {
		@apply h-20 !important;
	}
	&.spacer-6 {
		@apply h-24 !important;
	}

	@screen md {
		&.spacer-md-1 {
			@apply h-4 !important;
		}
		&.spacer-md-2 {
